.contacts {
  background: #eaeaea;
  position: relative;

  &--gray {
    background-color: @color_gray;
  }
}

.contacts___description-wrapper {
  @media (min-width: @desktop-width) {
    width: 100%;
    height: 100%;
    position: absolute;
  }
}

.contacts___description {
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
  padding-top: 12px;
  padding-bottom: 12px;

  @media (min-width: @desktop-width) {
    justify-content: flex-start;
    column-gap: 0;
    padding: 0;
    height: 100%;
    display: flex;
    align-items: center;
    background: none;
  }
}

.contacts__title {
  .special-text(16px; 20px);
  color: #111111;
}

.contacts__address {
  .base-text();
}

.contacts__title,
.contacts__address {
  @media (min-width: @desktop-width) {
    background: #ffffff;
    width: 190px;
    height: 165px;
    padding: 40px;
    display: grid;
    place-items: center;
  }
}
